@font-face {
    font-family: 'Outfit';
    src: url(../assets/webfonts/Font-face/Outfit-Regular.woff2) format('woff2');
    src: url(../assets/webfonts/Font-face/Outfit-Regular.ttf) format('ttf');
    font-weight: 400;
}

@font-face {
    font-family: 'Outfit';
    src: url(../assets/webfonts/Font-face/Outfit-Bold.woff2) format('woff2');
    src: url(../assets/webfonts/Font-face/Outfit-Bold.ttf) format('ttf');
    font-weight: 700;
}